.approach {
  position: relative;
  margin: -240px -227px 0 -106px;
  padding: 204px;
  border-radius: 100px 0 0 100px;

  background-color: $c-pink;

  &__text {
    max-width: 362px;
  }

  &__title {
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $c-white;

    margin-bottom: 16px;
  }

  &__heading {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;

    margin-bottom: 32px;
    color: $c-white;
  }



  &__image {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 687px;
    height: 680px;
  }
}

.appr-option {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:not(:last-child) {
    margin-bottom: 64px;
  }

  &__circle {
    display: flex;
    height: 64px;
    width: 64px;
    border-radius: 50%;
    background-color: $c-white;
  }

  &__icon {
    margin: auto;
    height: 24px;
    width: 24px;
  }

  &__text {
    max-width: 264px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 150%;
    color: $c-white;
  }
}