.footer {
  display: flex;
  justify-content: space-between;

  padding: 180px 270px 80px;
  background-color: $c-blue;
  border-radius: 0 200px 0 0;

}

.form {
  display: flex;
  flex-direction: column;
  max-width: 460px;

  &__heading {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: $c-white;
  }

  &__input {
    position: relative;
    background-color: $c-blue;
    color: $c-white;
    height: 41px;
    width: 100%;

    &::placeholder {
      color: $c-white;
    }
  }

  &__button {
    width: 226px;
    height: 56px;
    background-color: $c-pink;
    border-radius: 64px;
    
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    color: $c-white;
  }
}

.input__wrap {
  position: relative;
  margin-bottom: 48px;
  &:before {
    position: absolute;
    content: "";
    height: 2px;
    background-color: $c-white;
    border-color: $c-white;
    width: 100%;
    bottom: -2px;
    right: 0;

  }
}

.contact {
  &__heading {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: $c-white;

    margin-bottom: 56px;
  }

  &__title {
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;

    letter-spacing: 3px;
    text-transform: uppercase;
    opacity: 0.5;

    margin-bottom: 8px;
  }

  &__text {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: $c-white;
  }
}