.testimonials {

  &__title {
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $c-white-blue;
  }

  &__heading {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: $c-dark-blue;

    margin-bottom: 82px;
  }

  &__list {
  display: grid;
  grid-template-columns: repeat(3, 314px);
  justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px;
  }

  &__photo {
    position: relative;
    margin-bottom: 82px;
  }

  &__image {
    position: relative;
    object-fit: cover;
    object-position: center;
    height: 140px;
    width: 140px;
    border-radius: 50%;
    z-index: 1;
  }

  &__oval {
    position: absolute;
    width: 139px;
    height: 56px;
    border-radius: 500px;
    transform: rotate(-60deg);

    &--blue {
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #56CCF2;
    }

    &--yellow {
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #FCC304;
    }

    &--pink {
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #F25656;
    }

    &--left {
      top: -10px;
      left: -40px;
    }

    &--right {
      bottom: -10px;
      right: -40px;
    }
  }

  &__quotes {
    margin-bottom: 10px;
  }

  &__text {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    color: $c-light-blue;

    margin-bottom: 16px;
  }

  &__name {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    color: $c-dark-blue;
  }

  &__work {
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $c-white-blue;
  }
}